.login-fondo {
  display: flex;
  height: 100vh;
  background-color: aqua;
  background: url('../images/fondo-login.png') center no-repeat;
  background-size: 100% 100%;
}
.bg-gray {
  background-color: #d9d9d9!important;
  color: #323232!important;
}
.login-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  padding-left: 10%;
}
.logo {
  width: 350px;
  height: 100px;
  margin-bottom: 5%;
  padding: 0px 40px;
}
.form-login {
  background-color: #ffffff;
  padding: 30px;
  padding-bottom: 50px;
  border-bottom-left-radius: 35px;
  border-bottom-right-radius: 35px;
  text-align: center;
  width: 350px;
}
.title-login {
  color: #767676;
  margin-bottom: 40px;
  display: block;
  font-size: 24px;
  font-family: Roboto-Bold;
}
.input-login {
  width: 100%;
  padding: 15px;
  border-radius: 10px;
  border: 1px solid #d9d9d9;
  margin-top: 10px;
  display: block;
  outline: 0;
}
.input-login:focus {
  border: 1px solid #00d0f7  
}
.btn-submit {
  display: block;
  padding: 10px;
  width: 100%;
  border-radius: 10px;
  margin-top: 40px;
  margin-bottom: 10px;
  border: none;
  outline: 0;
  background-color: #f8931f;
  color: white;
  font-size: 18px;
}
.error-message {
  color: #ea4131;
  font-size: 12px;
  padding: 10px;
  transition: 1s;
  opacity: 0;
  height: 0px;
}

.link {
  font-family: Roboto-Medium, 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  outline: 0;
  border: none;
  background: transparent;
  color: #1d70b7;
  font-size: 14px;
  text-decoration: none;
}