.main {
    display: grid;
    height: 100vh;
    width: 100vw;
    grid-template-areas: "headerLogo header header"
                         "aside body body"
                         "aside body body";
    grid-template-rows: minmax(min-content, .4fr) 4fr;
    grid-template-columns: 200px 200px 4fr;
    transition: 1s;
}

@media (max-width: 800px) {
    .main {
        grid-template-areas: "headerLogo headerLogo  header"
            "aside body body"
            "aside body body";
        grid-template-rows: minmax(min-content, .4fr) 4fr;
        grid-template-columns: 45px 155px 4fr;
        transition: 1s;
    }
}

.headerLogo {
    grid-area: headerLogo;
    background-color: #1d6fb8;
    display: flex;
    align-items: center;
    justify-content: center;
    border-right: 1px solid #f9f9f950;
    border-bottom: 1px solid #f9f9f950;
}
.headerImageLogo {
    width: 60%;
    height: 60%;
}

/** Header */
.header > * {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    grid-area: header;
    background-color: #1d6fb8;
}
.icon-header-container {
    padding: 0px 30px;
    border-right: 1px solid #ffffff50;
}
.user-name {
    color: #ffffff;
    padding: 0px 5px;
    font-size: 16px;
}


/** Aside */
.aside { display: flex; flex-direction: column; grid-area: aside; background-color: #1d6fb8;}
@media (max-width: 800px) {
    .aside {
        width: 45px;
        z-index: 100;
        transition: .3s;
    }
    .aside:hover {
        width: 200px;
        transition: .3s;
    }
}
.list-nav {
    list-style-type: none;
    padding: 0px;
    margin: 0px;
    overflow: hidden;
}   
.list-element-nav {
    border-bottom: 1px solid #f9f9f950;
}
.list-item {
    display: flex;
    align-items: center;
    padding: 10px;
    text-decoration: none;
}
.list-item:hover {cursor: pointer;}
.btn {
    outline: 0;
    border: none;
}
.btn-menu-show {
    width: 100%;
    background-color: transparent;
}
.menu-oculto {
    display: none;
    padding: 10px 5px;
    padding-left: 30%;
    background-color: #29aae3;
}
.active-menu {
    display: block;
}
.active-menu-hover {
    background-color: #ffffff50;
}

.link-nav {
    text-align: left;
    color: #ffffff;
    padding: 5px 0px;
    display: block;
    font-size: 11px;
    font-family: Roboto-light;
    text-decoration: none;
    outline: 0;
    width: 100%;
    border: none;
    background: transparent;
}
.span-text {
    margin-left: 10px;
    color: #ffffff;
    padding: 0px 5px;
    font-size: 14px;
}
.logout-container {
    flex: .2;
    padding: 10px 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.text-right {
    text-align: right;
}
.margin-sprm {
    margin: 0;
    padding: 0;
}
/** body */
.relative {position: relative;}
.space-between { justify-content: space-between; }
.flex {display: flex; align-items: center;}
.flex-end {display: flex; justify-content: flex-end;}
.body {grid-area: body; background-color: white;}
