.banner-title-container {
    margin: 0px 20px;
    align-items: center;
    height: 50px;
    display: flex;
    justify-content: space-between;
    border-bottom: 3px solid #1d6fb8;
}

.banner-title {
    color: #29ABE2;
    padding-left: 20px;
    font-family: Roboto-Bold;
}
.banner-subtitle {
    margin: 0px;
    margin-top: 20px;
}

.banner-span {
    font-family: Roboto-Bold;
    color: #29ABE2;
    padding-right: 10px;
    font-size: 12px;
}

.banner-select {
    width: 200px;
    border-radius: 10px;
    background-color: #f3f3f3;
    padding: 2px 5px;
    color: #aaaaaa;
    outline: 0px;
}

.container-spacing { margin: 5% 8% }

.box-container {
    display: flex;
    flex-wrap: wrap;
    margin: 15px 0px;
}
.box-item {
    align-items: center;
    flex-direction: column;
    justify-content: center;
    display: flex;
    width: 100px;
    height: 80px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px 3px #00000025;
    margin-right: 20px;
    margin-bottom: 20px;
}
.box-item:last-child { margin-right: 0px; }
.box-span {
    display: block;
    font-size: 10px;
    font-family: Roboto-Bold;
    text-align: center;
    color: #1d6fb8;
}

.btn-box {
    border-radius: 20px;
    width: 50%;
    font-size: 10px;
    font-family: Roboto-Regular;
    background-color: #1d6fb8;
    padding: 0;
    color: #fff;
    margin-top: 5px;
}
.btn-add {
    background-color: #1d6fb8;
    color: #ffffff;
    border-radius: 8px;
    padding: 10px;
    margin: 0px 20px;
    width: 150px;
}

.container-input-row {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-bottom: 20px;
    margin: 5px 20px;
}
.border-bt-blue {border-bottom: 1px solid #29ABE2;}
.container-input-two {
    padding: 2px;
    border: 1px solid #9a9a9a;
    border-radius: 5px;
    width: 49%;
    margin: 5px 0px;
    display: flex;
    align-items: center;
}
.label-input-two {
    padding: 0px 10px;
    font-family: Roboto-Bold;
    color: #757575;
    font-size: 12px;
}
.input-two {
    background-color: #d1d2d4;
    border: none;
    outline: 0;
    padding: 5px 10px;
    width: max-content;
    flex: auto;
    font-family: Roboto-Bold;
    color: #757575;
    font-size: 12px;
    border-radius: 3px;
}

/** Table */
.table-list {
    padding: 20px;
    width: 100%;
}
.input-search {
    border: 1px solid #dddddd;
    outline: 0;
    font-size: 12px;
    padding: 3px 10px;
    padding-right: 30px;
    height: 25px;
}
.btn-search {
    position: absolute;
    right: 0;
    top: 0;
    background-color: #1d6fb8;
    border-radius: 0pc 8px 8px 0px;
    padding: 3px 7px;
    height: 25px;
    display: flex;
}
.table-contain {
    width: 100%;
    background-color: #f3f3f3;
    overflow-x: auto;
    margin: 10px 0px;
}
.table-contain table {width: 100%; border-collapse: collapse;}
.table-contain table th {
    border: 1px solid #9a9a9a;
    font-family: Roboto-Bold;
    color: #29ABE2;
    padding: 5px 10px;
    font-size: 12px;
}
.table-contain table td {
    border: 1px solid #9a9a9a;
    text-align: center;
    font-family: Roboto-Regular;
    color: #555555;
    padding: 5px 10px;
    font-size: 10px;
}